@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);
body {
  background-color: rgb(234, 236, 240);
  color: #000000;
  font-family: "Noto Sans JP", sans-serif;
  font-style: normal;
  line-height: 1.33333;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

li {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li {
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.App {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 16px;
  padding-bottom: 24px;
}

.Searchbar_searchbar__21A6G {
  top: 0;
  left: 0;
  position: sticky;
  z-index: 1100;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 64px;
  padding-right: 24px;
  padding-left: 24px;
  padding-top: 12px;
  padding-bottom: 12px;
  color: #fff;
  background-color: #3f51b5;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

/*
 * Стили компонента SearchForm
 */
.Searchbar_searchForm__3bEFQ {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 600px;
  background-color: #fff;

  border-radius: 3px;
  overflow: hidden;
}

.Searchbar_button__1ZaZK {
  display: inline-block;
  width: 48px;
  height: 48px;
  border: 0;
  background-image: url("https://image.flaticon.com/icons/svg/149/149852.svg");
  background-size: 40%;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0.6;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  outline: none;
}

.Searchbar_button__1ZaZK:hover {
  opacity: 1;
}

.Searchbar_label__2SwTC {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  -webkit-clip-path: inset(50%);
          clip-path: inset(50%);
  border: 0;
}

.Searchbar_input__1SBLO {
  display: inline-block;
  width: 100%;
  font: inherit;
  font-size: 20px;
  border: none;
  outline: none;
  padding-left: 4px;
  padding-right: 4px;
  text-align: center;
}

.Searchbar_SearchForm-input__1m8eH::-webkit-input-placeholder {
  font: inherit;
  font-size: 18px;
}

.Searchbar_SearchForm-input__1m8eH:-ms-input-placeholder {
  font: inherit;
  font-size: 18px;
}

.Searchbar_SearchForm-input__1m8eH::placeholder {
  font: inherit;
  font-size: 18px;
}

.ImageGallery_gallery__2apN4 {
  display: grid;
  max-width: calc(100vw - 48px);
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 16px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}

.ImageGalleryItem_imageItem__1pW8m {
  border-radius: 2px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.ImageGalleryItem_galleryItemImage__3t1Aq {
  width: 100%;
  height: 260px;
  object-fit: cover;
  transition: -webkit-transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1), -webkit-transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.ImageGalleryItem_galleryItemImage__3t1Aq:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
  cursor: zoom-in;
}

.Button_button__1kpf7 {
  margin-top: 15px;
  padding: 8px 16px;
  border-radius: 2px;
  background-color: #3f51b5;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  text-align: center;
  display: inline-block;
  color: #fff;
  border: 0;
  text-decoration: none;
  cursor: pointer;
  font-family: inherit;
  font-size: 18px;
  line-height: 24px;
  font-style: normal;
  font-weight: 500;
  min-width: 180px;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.Button_button__1kpf7:hover,
.Button_button__1kpf7:focus {
  background-color: #303f9f;
}
.Button_wrapper__1p4zY {
  text-align: center;
}

.Modal_overlay__3xmtr {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1200;
}

.Modal_modal__a6jko {
  max-width: calc(100vw - 48px);
  max-height: calc(100vh - 24px);
}

.Container_container__CP-wE {
  max-width: 1400px;
  margin-left: auto;
  margin-right: auto;
  padding: 15px;
}

